import * as React from 'react';

import {
    DropdownType,
    DropdownWidget,
} from '@tableau/widgets';

import { Button } from '@tableau/tableau-ui';

// Shows if setting has not yet been configured
class Selector extends React.Component<any, any> {
    public render() {
        const menuItemInfos = this.props.list.map((item: string) => ({ value: item, displayValue: item }));

        return (
            <div className='selector'>
                <DropdownWidget
                    containerStyle={{ marginLeft: 0, width: '100%', flex: 1 }} 
                    disabled={!this.props.enabled}
                    dropdownType={DropdownType.Line}
                    menuItemInfos={menuItemInfos}
                    onSelect={this.props.onChange}  // <-- this one
                    selectedValue={this.props.selected}
                    testId='select'
                />
                <Button
                    kind='filledGreen'
                    onClick={this.props.onClick}
                    disabled={!this.props.enabled || this.props.selected === ''}
                    style={{ marginLeft: '12px' }}
                >Set</Button>
            </div>
        )
    }
}

export default Selector;


